.home ,.about ,.project ,.skill ,.contact,.calender{
    height:fit-content;
    padding: 20px 0px;
    border: 3px solid rgb(225, 29, 72);
   
   

}

@media screen and (max-width: 820px){
    .home ,.about ,.project ,.skill ,.contact,.calender{
        height:fit-content;
        border: 3px solid rgb(225, 29, 72);
        width: 90%;
        margin: auto;
     
       
    
    }
}

@media screen and (max-width: 420px){
    .home ,.about ,.project ,.skill ,.contact,.calender{
        height:fit-content;
        border: 3px solid rgb(225, 29, 72);
       
      
         
    
    }

}


