.project-heading{
 text-align: center;
 padding: 4rem 0 2rem 0;

 font-size: 35px;
 text-decoration: underline;
 color:rgb(225, 29, 72);
}

.project-container{
    max-width:90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}
.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
}

.project-card img{
   width: 100%;
}
.project-title{
    color: white;
    padding: 1rem 0;
}
.pro-details p{
  padding-bottom: 1.2rem;
  font-size: 1.1rem;
  text-align: justify;
  height: 110px;
}
.pro-btn{
    display: flex;
    justify-content:space-between;
    padding: .5rem 0;
   
}
.pro-btn .btn{
    padding: 0.5 1rem;
  
 
}

@media screen and (max-width: 820px){
    .project-container{
        width: 80%;
        margin: auto;
        grid-template-columns: 1fr;
        height: fit-content;
       
      
     }
     .work-container{
        width: 100%;
    }

     .project-card img{
         width: 100%;
         height: 350px;
     }

     .pro-details p{
        
        font-size:23px;
       
        height: 150px;
      }

     #tectstack{
        color:rgb(225, 29, 72);
        margin-top: 20px;
        font-size: 20px;
        
    }

    .pro-btn{
        display: flex;
        justify-content:space-around;
        margin-top: 40px;
       
       
    }

    .project-card{
        background: #1a1919;
        width: 95%;
       
        
    }
    
}


@media screen and (max-width: 420px){
    .project-container{
        width:100%;
       
       
        grid-template-columns: 1fr;
    }
    .work-container{
        width: 100%;
    }

    #tectstack{
         width: 90%;
         color:rgb(225, 29, 72);
         margin-top: 20px;
         font-size: 14px;
         text-align: left;
        
    }


    .project-card img{
         width:100%;
         height: 200px;
     }

     .pro-details p{
        padding-bottom: 1.2rem;
        font-size: 1.1rem;
        text-align: justify;
        height: fit-content;
        width:100%;
      }
     
      .pro-btn{
        display: flex;
        justify-content:space-between;
        flex-direction: column;
        font-size: 15px;
        gap: 20px;
        width: 50%;
       
       
    }

    .project-card{
        background: #1a1919;
        width: 90%;
       
        
    }
    .btn{
        font-size: 12px;
    }

    .project-title{
        color: white;
        font-size: 16px;
    }
      


}


    
