.skillcss{
    border: 1px solid red;
   
   max-width: fit-content;
   text-align: center;
   margin-bottom: 20px;
}

.skillcss:hover{
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color:yellow;
   
}

.skillstyle{
    width: 80%;
    margin: auto;
    display: grid;
    margin-top: 40px;
    grid-template-columns: repeat(5,1fr);
    gap: 20px;
  
    
}

.skillheading>h1{
   
    text-align: center;
    font-size: 35px;
   
    color:rgb(225, 29, 72);
    margin-top: 30px;
    text-decoration: underline;

}

.skillcss>img{
    width:70%;
    height: 140px;
}

@media screen and (max-width:820px) {
    .skillheading{
         width: 65%;
         margin: auto;
         padding-bottom: 20px;
    }

    .skillheading>h1{
       
       
    

    
       
        font-size: 38px;
       
        color:rgb(225, 29, 72);
        margin-top: 50px;
        text-decoration: underline;
    
    }

    .skillstyle{
        width: 90%;
       
        display: grid;
        margin-top: 40px;
        grid-template-columns: repeat(2,1fr);
        gap: 20px;
      
        
    }
    

    .skillstyle img{
        width: 90%;
        height: 150px;
       
    }
    .skillcss{
      
   
        
         
          width:95%;
          height:100%;
         
    }
    .skillcss>h4{
        font-size: 15px;
    }
    
}


@media screen and (max-width:420px) {
    .skillheading{
        width: 100%;
        text-align: center;
       
    }

    .skillstyle{
        width: 100%;
      
        display: grid;
        margin-top: 40px;
        grid-template-columns:100%;
        grid-template-rows:repeat(10,200px);
        gap: 20px;
      
      
     
        
    }
     
    .skillheading>h1{
       
     
      
        font-size: 25px;
       
        color:rgb(225, 29, 72);
        margin-top: 30px;
        text-decoration: underline;
         text-align: center;
        
        
    
    }
   

    .skillcss>img{
        width:100%;
        height: 150px;
    }

    .skillcss>h4{
        font-size: 18px;
    }

    .skillcss{
     
       
       width:90%;
       margin: auto;
      
      
    }
    
   

    
       
      
    
    

    
}

