
.contactcss{
    text-align: center;
    width: 100%;

    margin-bottom: 20px;
    padding-bottom: 30px;
   
    

}

.contactcss>h1{
    font-size: 35px;
    text-decoration: underline;
   
    color:rgb(225, 29, 72);
    margin-top: 50px;
}

.icons{
  
   
   
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    margin-top: 50px;
    align-content: center;
    align-items: center;
   
   
}





   





 
    @media screen and (max-width:820px){

        .contactcss{
            width: 100%;

        }
    
        .icons{
        
        
            width: 90%;
            margin: auto;
            
            
            display: flex;
            flex-direction: column;
           
            margin-top: 40px;
           
            font-size: 16px;
            
           
        
        
        
        
        
        }

    }   

    @media screen and (max-width:420px){

        .contactcss{
            width: 100%;
            align-items: center;
             
            
            

        }
    
         .icons{
        
        
           
          
            
             width: 90%;
           
            margin-top: 40px;
           
            font-size: 11px;
            
          
           
        
        
        
        
        
        } 

       

        .contactcss>h1{
            font-size: 25px;
            text-decoration: underline;
           
            color:rgb(225, 29, 72);
            margin-top: 50px;
        }

        .iconstyle{
            display: flex;
            flex-direction: column;
            width: 80%;
        }
        .iconstyle>h1+h1{
            width: 70%;
            font-size: 20px;
            text-align: left;
        }
       
        

    }   

